@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: 'Roboto', sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  padding: 0;
  font-size: 16px;
  line-height: 1.56;
  background-color: #151B1F;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a,.Link {
  color: inherit;
  text-decoration: none;
  transition: 1s;
}

* {
  box-sizing: border-box;
}

a, p, h1, h2, h3, h4, h5, label {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

* , *:after, *:before {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif, 'Noto Sans SC', sans-serif;
  scroll-behavior: smooth;
}

.AppRoot {
  height: 100%;
  width: 100%;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

* , *:after, *:before {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif, 'Noto Sans SC', sans-serif;
  scroll-behavior: smooth;
}

.toolbar-link {
  color: #84a7c4;
}

.toolbar-link:hover {
  color: white;
}

.PageHeader {
  min-height: 90vh;
}

.PageHeader > .TextContent > .Description {
  max-width: 40vw;
  margin-top: 20px;
}

.PageHeader > .TextContent > .Description2 {
  max-width: 40vw;
  margin-top: 20px;
}

.image-gallery {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 100vh; /* Set the desired height */
}

.image-container {
  flex: 1;
  z-index: 1000;
  overflow: hidden;
  position: relative;
  transition: transform 0.3s ease; /* Add a smooth transition effect */
}

.image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Hover effect */
.image-container:hover {
  transform: scale(1.1); /* Increase the scale on hover */
  z-index: 1; /* Move the hovered image to the front */
}

.notFoundContent {
  height: 47vh;
}